@font-face {
    font-family:'MuseoSans900';
    src: url('../fonts/MuseoSansCyrl-900.woff2') format('woff2'),
    url('../fonts/MuseoSansCyrl-900.woff') format('woff'),
    url("../fonts/MuseoSansCyrl-900.otf") format("opentype");
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    unicode-range: U+0020-0525;
}

