.lost-item {
    lost-column: 1/2;
    text-align: center;
    height: 100px;
    background: red;
    margin-bottom: 20px;
    line-height: 100%;
    line-height: 100px;
    .lost-item.blue {
        background: blue;
    }
    .lost-item.green {
        background: green;
    }
    .lost-item.yellow {
        background: yellow;
        lost-column: 1/4;
    }
    .lost-item.black {
        background: black;
        lost-column: 1/6;
    }
}
