// /show and hide classes

//global styles
h2 {
    max-width: 760px;
    color: $dark;
    font-family: $m300;
    font-size: 36px;
    line-height: 44px;
    text-transform: uppercase;
    text-align: center;
    margin: 0 auto 11px auto;
    padding-top: 16px;
    font-weight: 300;
    letter-spacing: 0.5px;
    @include respond-to(tablet) {
        font-size: 26px;
        line-height: 32px;
        padding-top: 24px;
    }
    @include respond-to(mobile) {
        font-size: 22px;
        line-height: 28px;
        padding-top: 40px;
        max-width: 300px;
    }
}
p {

}
/* For modern browsers */

.clearfix:before,
.clearfix:after {
    content: "";
    display: table;
}

.container {
    width: 960px;
    margin: 0 auto;
    @include respond-to(tablet) {
        width: 640px;
    }
    @include respond-to(mobile) {
        width: 300px;
        padding: 0 10px;
    }
}

//SECTION-1
.top-line {
    padding-top: 10px;
    padding-bottom: 10px;
}
.top-line__text {
    display: inline-block;
    vertical-align: top;
    letter-spacing: 0.5px;
    @include respond-to(mobile) {
        text-align: center;
        width: 100%;
    }
    p {
        color: $red;
        font-family: $m900;
        font-size: 20px;
        line-height: 26px;
        text-transform: uppercase;
        @include respond-to(mobile) {
            font-size: 15px;
            letter-spacing: 0.7px;
            line-height: 20px;
        }
    }
    span {
        display: block;
        color: $dark;
        font-family: $m900;
        font-size: 20px;
        line-height: 26px;
        text-transform: uppercase;
        @include respond-to(mobile) {
            font-size: 15px;
            letter-spacing: 0.7px;
            line-height: 20px;
        }
    }
}
.top-line__timer__wrapper {
    display: inline-block;
    vertical-align: top;
    margin-left: 22px;
    @include respond-to(tablet) {
        margin-left: 4px;
    }
    @include respond-to(mobile) {
        display: block;
        width: 260px;
        margin: 6px auto 0 auto;
    }
}
.top-line__timer {
    width: 97px;
    height: 48px;
    background-color: $orange;
    text-align: center;
    display: inline-block;
    vertical-align: top;
    padding-top: 9px;
    margin-right: 10px;
    position: relative;
    @include respond-to(tablet) {
        width: 71px;
        height: 46px;
    }
    @include respond-to(mobile) {
        width: 71px;
        height: 40px;
    }
    span {
        &:first-of-type {
            font-size: 28px;
            font-family: $m900;
        }
        display: block;
        width: 100%;
        color: $white;
        font-family: $m700;
        line-height: 22px;
        font-size: 13px;
        @include respond-to(mobile) {
            line-height: 20px;
        }
    }
    &:last-of-type {
        margin-right: 7px;
        @include respond-to(tablet) {
            margin-right: 0;
        }
    }
}
.top-line__text_last {
    width: 200px;
    @include respond-to(tablet) {
        display: none;
    }
    @include respond-to(mobile) {
        display: none;
    }
}
.top-line__hour {
    position: relative;
    .top-line__timer__points {
        position: absolute;
        width: 6px;
        top: 13px;
        right: -9px;
        color: #222222;
        font-family: $m900;
        font-size: 28px;
        line-height: 25px;
    }
}
.top-line__min {
    position: relative;
    .top-line__timer__points {
        position: absolute;
        width: 6px;
        top: 13px;
        right: -9px;
        color: #222222;
        font-family: $m900;
        font-size: 28px;
        line-height: 25px;
    }
}
.top-line__sec {
    span {
        &:first-of-type {

        }
    }
}
.top-line__timer__min {
    span {
        &:first-of-type {

        }
    }
}
.top-line__timer__sec {
    span {
        &:first-of-type {

        }
    }
}

//SECTION-2
.block-2 {
    background: url(../img/top-img.jpg) center 0 no-repeat;
    padding-bottom: 320px;
    @include respond-to(tablet) {
        background: url('../img/640-top.jpg') center center no-repeat;
        padding-bottom: 204px;
    }
    @include respond-to(mobile) {
        background: url('../img/320-bg.jpg') center 0 no-repeat;
        padding-bottom: 0;
    }
    .container {
        @include respond-to(mobile) {
            width: 100%;
            padding: 0;
        }
    }
}
.clock {
    width: 470px;
    float: right;
    margin-top: 28px;
    @include respond-to(tablet) {
        width: 440px;
        margin-top: 25px;
        margin-right: 32px;
    }
    @include respond-to(mobile) {
        width: 100%;
        float: none;
        margin-top: 334px;
    }
    img {
        max-width: 162px;
        height: auto;
        margin: 0 auto;
        display: block;
    }
    &>img {
        display: none;
        @include respond-to(mobile) {
            display: block;
            width: 300px;
            height: 240px;
            margin-bottom: 25px;
            max-width: 100%;
        }
    }
    h1 {
        color: $white;
        font-family: $m700;
        font-size: 36px;
        line-height: 40px;
        text-transform: uppercase;
        margin: 20px 0 0 0px;
        text-align: center;
        @include respond-to(tablet) {
            font-size: 23px;
            margin: 16px 7px 0 0px;
            text-align: right;
            line-height: 28px;
        }
        @include respond-to(mobile) {
            font-family: $m900;
            font-size: 22px;
            line-height: 34px;
            margin: 0 0 0 0px;
        }
    }
    p {
        color: $white;
        font-family: $m300;
        font-size: 36px;
        line-height: 34px;
        margin-top: 4px;
        text-align: center;
        @include respond-to(tablet) {
            font-size: 23px;
            line-height: 24px;
            margin-top: 4px;
            margin-right: 0;
            text-align: left;
            padding-left: 144px;
            letter-spacing: 0.5px;
        }
        @include respond-to(mobile) {
            font-size: 22px;
            line-height: 22px;
            width: 320px;
            margin: 0 auto 0 auto;
            padding-bottom: 4px;
        }
    }
}

.mobile-l-green {
    @include respond-to(mobile) {
        background: #6b6b35;
        padding-top: 5px;
        padding-bottom: 5px;
    }
    img {
        @include respond-to(mobile) {
            display: none;
        }
    }
}

.mobile-d-green {
    @include respond-to(mobile) {
        background: #38392a;
        margin-bottom: 25px;
    }
}
.mobile-d-green__container {
    @include respond-to(mobile) {
        width: 304px;
        margin: 0 auto;
        padding-bottom: 23px;
    }
}

.old__price {
    display: inline-block;
    vertical-align: top;
    margin-top: 35px;
    margin-left: 90px;
    @include respond-to(tablet) {
        margin-top: 22px;
        margin-left: 141px;
    }
    @include respond-to(mobile) {
        margin-top: 7px;
        margin-left: 0;
    }
    p {
        color: $white;
        font-family: $m300;
        font-size: 20px;
        line-height: 26px;
        text-align: center;
        padding-left: 12px;
        width: auto;
    }
    span {
        display: block;
        color: $white;
        font-family: $m500;
        font-size: 36px;
        line-height: 50px;
        text-decoration: line-through;
    }
}
.new__price {
    display: inline-block;
    vertical-align: top;
    margin-top: 35px;
    margin-left: 22px;
    @include respond-to(tablet) {
        margin-top: 22px;
        margin-left: 19px;
    }
    @include respond-to(mobile) {
        margin-top: 7px;
    }
    p {
        color: $white;
        font-family: $m300;
        font-size: 20px;
        line-height: 26px;
        width: auto;
        padding-left: 12px;
        @include respond-to(mobile) {
            text-align: left;
            padding-left: 0;
        }
    }
    span {
        display: block;
        max-width: 165px;
        color: #cbcb75;
        font-family: $m900;
        font-size: 36px;
        line-height: 50px;
    }
}
.clock__button {
    width: 300px;
    height: 65px;
    box-shadow: 0 4px 0 rgba(188, 51, 38, 0.75), inset 0 2px 0 rgba(222, 109, 93, 0.75);
    border-radius: 30px;
    background-color: #f95428;
    background-image: linear-gradient(to top, #e23724 0%, #e63520 100%);
    color: $white;
    font-family: $m500;
    font-size: 20px;
    font-weight: 500;
    line-height: 65px;
    text-align: center;
    border: 0;
    display: block;
    margin: 18px auto 0 auto;
    @include respond-to(tablet) {
        margin: 11px 0 43px 143px;
    }
    @include respond-to(mobile) {
        margin: 5px auto 0 auto;
    }
}

//SECTION-3
.block-3 {
    background: url('../img/block-2-bg.jpg') 0 0 repeat;
    p {
        max-width: 920px;
        color: $dark;
        font-family: $m300;
        font-size: 20px;
        line-height: 26px;
        text-align: center;
        margin: 0 auto 40px auto;
        @include respond-to(tablet) {
            font-size: 16px;
            line-height: 24px;
        }
        @include respond-to(mobile) {
            font-size: 16px;
            line-height: 22px;
            max-width: 300px;
        }
    }
}
.elit {
    width: 289px;
    margin-left: 30px;
    text-align: center;
    padding-bottom: 60px;
    display: inline-block;
    vertical-align: top;
    &:first-of-type {
        margin-left: 0;
    }
    @include respond-to(tablet) {
        width: 189px;
        margin-left: 30px;
    }
    @include respond-to(mobile) {
        width: 300px;
        padding-bottom: 30px;
        margin: 0 auto;
        display: block;
    }
    img {
        max-width: 100%;
        height: auto;
        margin-bottom: 3px;
        @include respond-to(tablet) {
            max-width: 120px;
        }
        @include respond-to(mobile) {
            max-width: 120px;
        }
    }
    p {
        color: $dark;
        font-family: $m300;
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 0;
        @include respond-to(tablet) {
            font-size: 15px;
            line-height: 22px;
            margin-top: 10px;
        }
        @include respond-to(mobile) {
            font-size: 16px;
            line-height: 22px;
            margin-top: 10px;
        }
    }
    h3 {
        color: $dark;
        font-family: $m900;
        font-size: 20px;
        line-height: 36px;
        text-transform: uppercase;
        letter-spacing: 0.5px;
        margin-bottom: 3px;
        @include respond-to(tablet) {
            font-size: 14px;
            line-height: 22px;
        }
        @include respond-to(mobile) {
            font-size: 14px;
            line-height: 22px;
        }
    }
}

//SECTION-4
.block-4 {
    background: url('../img/clock-bg.jpg') 0 0 repeat;
    padding-bottom: 20px;
    h2 {
        max-width: 830px;
        margin: 0 auto;
        color: $white;
        font-family: $m900;
        font-size: 36px;
        line-height: 44px;
        text-transform: uppercase;
        padding-top: 22px;
        padding-bottom: 20px;
        @include respond-to(mobile) {
            font-size: 24px;
            line-height: 32px;
            padding-left: 10px;
            padding-right: 10px;
        }
    }
    img {
        display: inline-block;
        vertical-align: top;
        width: 524px;
        height: 362px;
        margin-left: -44px;
        @include respond-to(tablet) {
            display: block;
            margin: 0 auto;
        }
        @include respond-to(mobile) {
            width: 300px;
            height: auto;
            margin: 20px auto 0 auto;
        }
    }
    .block-4__content__green {
        background: #666633;
    }
}
.block-4__content {
    display: inline-block;
    vertical-align: top;
    margin-top: 43px;
    @include respond-to(tablet) {
        display: block;
        margin: 0 auto;
        width: 460px;
    }
    @include respond-to(mobile) {
        margin-top: 20px;
        text-align: center;
    }
    h3 {
        color: $white;
        font-family: $m700;
        font-size: 26px;
        line-height: 48px;
        text-transform: uppercase;
        @include respond-to(mobile) {
            font-size: 22px;
            line-height: 28px;
        }
    }
    ul {
        max-width: 460px;
        margin-top: 18px;
        @include respond-to(mobile) {
            max-width: 300px;
        }
        li {
            display: inline-block;
            vertical-align: top;
            margin-bottom: 27px;
            @include respond-to(mobile) {
                margin-bottom: 15px;
            }
            &:nth-child(odd) {
                width: 180px;
                margin-right: 43px;
                @include respond-to(mobile) {
                    width: 100%;
                    margin-right: 0;
                }
            }
            &:nth-child(even) {
                width: 229px;
                @include respond-to(mobile) {
                    width: 100%;
                }
            }
        }
        p {
            color: $white;
            font-family: $m700;
            font-size: 18px;
            line-height: 26px;
            text-transform: uppercase;
            @include respond-to(mobile) {
                font-size: 16px;
                line-height: 24px;
            }
        }
        span {
            color: #f34632;
            font-family: $m700;
            font-size: 18px;
            line-height: 26px;
            @include respond-to(mobile) {
                font-size: 16px;
                line-height: 24px;
            }
        }
    }
}

//SECTION-5
.block-5 {
    background: url('../img/block-2-bg.jpg') 0 0 repeat;
    padding-bottom: 40px;
    h2 {
        padding-top: 44px;
        margin-bottom: 25px;
        @include respond-to(mobile) {
            font-size: 26px;
        }
    }
    .deliverys {

    }
    .delivery {
        width: 300px;
        margin-left: 19px;
        text-align: center;
        display: inline-block;
        vertical-align: top;
        @include respond-to(tablet) {
            width: 196px;
        }
        @include respond-to(mobile) {
            width: 300px;
            display: block;
            margin: 0 auto 30px auto;
        }
        &:first-of-type {
            margin-left: 0;
        }
    }
    .delivery__content {
        background: #ffffff;
        margin-top: -58px;
        height: 150px;
        padding-top: 70px;
        @include respond-to(tablet) {
            height: 200px;
        }
    }
    img {
        width: 124px;
        height: 111px;
    }
    h3 {
        color: #000000;
        font-family: $m500;
        font-size: 20px;
        line-height: 17px;
        text-transform: uppercase;
        margin-bottom: 16px;
    }
    p {
        color: #000000;
        font-family: $m300;
        font-size: 16px;
        line-height: 22px;
        padding: 0 8px;
    }
}




