$w-wide: 480px;
$w-thin: 230px;
$h-tall: 480px;
$h-small: 230px;
.masonry {}

.hidden {
    display: none;
}

.grid {
    width: 1260px;
    margin: 0 auto;
     @include respond-to(tablet) {
         width: 768px;
     }
    @include respond-to(mobile) {
        width: 99%;
        margin: 0 auto;
    }
}

.grid-item {
    background: rgba(0,0,0, 0.5);
    margin-bottom: 20px;
    text-align: center;
    @include respond-to(mobile) {
        margin-bottom: 1%;
    }
    img {
        width: 100%;
        height: 100%;
        @include respond-to(mobile) {
            width: 100%;
            height: 100%;
        }
    }
}

.grid-item--width1 {
    width: $w-thin;
    height: $h-tall;
    @include respond-to(mobile) {
        width: 48%;
    }
    &.gigante {
        width: $w-thin * 2;
        height: $h-tall * 2;
    }
}

.grid-item--width2 {
    width: $w-wide;
    height: $h-small;
    @include respond-to(mobile) {
        width: 48%;
    }
    img {
        max-height: 250px;
    }
    &.gigante {
        width: $w-thin * 2;
        height: $h-tall * 2;
    }
}

.stamp {
    border: 1px solid red;
}

.grid-item p {
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
}

.show-elements {
    display: block;
    width: 200px;
    height: 60px;
    line-height: 60px;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
    border: 1px solid blue;
    color: brown;
    text-align: center;
    margin: 0 auto;
}
